type Key = string | number;

interface Map<V>{
    [key: Key]: V
}

type mappingfunc<V> = (element: V, key: Key) => any;

class Utils{
    static MapJSON<V> (mapToMap: Map<V>, mapfunc: mappingfunc<V>) {
        let mappedArray: any[] = [];
        let keys = Object.keys(mapToMap);
        keys.sort();
        keys.forEach(key => mappedArray.push(mapfunc(mapToMap[key], key)));
        return mappedArray;
    }
}

export default Utils;