import Matrix from "../Matrix";
import { MathObjectData, MatrixData } from "./IMathObject";
import MathObjectType from "./MathObjectType";


class SimpleMatrixData implements MathObjectData{
    
    name: string;
    type: MathObjectType = MathObjectType.MATRIX;
    value: MatrixData;

    constructor(name: string, data: number[][], rows: number, cols: number){
        this.value = {
            cols: cols,
            rows: rows,
            values: data,
            getMatrix: this.getMatrix
        };
        this.name = name;
    }

    getMatrix: () => Matrix = () => new Matrix(this.value.values, [this.value.rows, this.value.cols]);
}

export default SimpleMatrixData;