import * as jison from 'jison';
import { InputData } from '../Panels/OperationsPanel';

import ScalarObject from './MathObjects/ScalarObject';
import MathObjectFactory from './MathObjects/MathObjectFactory'

declare global {
   interface Window { 
      MathObjectFactory: any; 
      ScalarObject: any;
      inputData: InputData; 
   }
}

window.MathObjectFactory = window.MathObjectFactory || {};
window.ScalarObject = window.ScalarObject || {};

class MatrixCalculator{
   static parser: jison.Parser | undefined = undefined;

   static parse(value: string){
      window.MathObjectFactory = MathObjectFactory;
      window.ScalarObject = ScalarObject;
      if(this.parser === undefined){
         MatrixCalculator.parser = new jison.Parser({
            "comment": "Matrix Calculator",
            
            "lex": {
               "rules": [
                  ["\\s+",                    "/* skip whitespace */"],
                  ["[0-9]+(?:\\.[0-9]+)?",    "return 'NUMBER'"],
                  ["\\*",                     "return '*'"],
                  ["\\/",                     "return '/'"],
                  ["-",                       "return '-'"],
                  ["\\+",                     "return '+'"],
                  ["\\^",                     "return '^'"],
                  ["!",                       "return '!'"],
                  ["%",                       "return '%'"],
                  ["\\(",                     "return '('"],
                  ["\\)",                     "return ')'"],
                  ["\\|",                     "return '|'"],
                  ["PI\\b",                   "return 'PI'"],
                  ["E\\b",                    "return 'E'"],
                  ["(adj|Adj|ADJ)",           "return 'ADJ'"],
                  ["(tras|Tras|TRAS)",        "return 'TRAS'"],
                  ["(inv|Inv|INV)",           "return 'INV'"],
                  ["[A-Za-z]",                "return 'VARIABLE'"],
                  ["$",                       "return 'EOF'"]
               ]
            },
         
            "operators": [
               ["left", "+", "-"],
               ["left", "*", "/"],
               ["left", "^"],
               ["right", "!"],
               ["right", "%"],
               ["left", "UMINUS"]
            ],
         
            "bnf": {
               "expressions": [["e EOF",   "return $1"]],
         
               "e" :[
                  ["e + e",  "$$ = $1.add($3)"],
                  ["e - e",  "$$ = $1.subs($3)"],
                  ["e * e",  "$$ = $1.mult($3)"],
                  ["e e",  "$$ = $1.mult($2)"],
                  ["TRAS ( e )",  "$$ = $3.tras()"],
                  ["INV ( e )",  "$$ = $3.inv()"],
                  ["ADJ ( e )",  "$$ = $3.adj()"],
                  ["e ( e )",  "$$ = $1.mult($3)"],
                  ["e / e",  "$$ = $1.div($3)"],
                  ["e ^ e",  "$$ = $1.pow($3)"],
                  ["| e |",  "$$ = $2.abs();"],
                  ["e !",    "$$ = (function(n) { if(n==0) return MathObjectFactory.make(1); return arguments.callee(n.minus(1)).mult(n) })($1)"],
                  ["e %",    "$$ = $1.div(100)"],
                  ["- e",    "$$ = $2.uminus()", {"prec": "UMINUS"}],
                  ["( e )",  "$$ = (function (n) { n.name = '(' + n.name + ')'; return n;})($2)"],
                  ["NUMBER", "$$ = MathObjectFactory.make(yytext)"],
                  ["E",      "$$ = new ScalarObject(Math.E, 'e')"],
                  ["PI",     "$$ = new ScalarObject(Math.PI,'pi')"],
                  ["VARIABLE", "$$ = (function(n) { if(inputData[yytext] === undefined) {throw new Error('La variable ' + yytext + ' no está definida')} else {return MathObjectFactory.make(inputData[yytext]) }})(yytext)"]
               ]
            }
         });
      }
      return (MatrixCalculator.parser?.parse(value))
   }

}

export default MatrixCalculator