import IMathObject, { MathObjectData, MatrixData } from "./IMathObject";
import MathObjectType from "./MathObjectType";
import MatrixObject from "./MatrixObject";
import ScalarObject from "./ScalarObject";

class MathObjectFactory{
    static make(value: string | MathObjectData): IMathObject{
        if(typeof value === 'string'){
            return new ScalarObject(parseFloat(value), value);
        }
        return value.type === MathObjectType.MATRIX ? new MatrixObject(value.value as MatrixData, value.name ?? "") : new ScalarObject(value.value as number, value.name);
    }
}

export default MathObjectFactory;