import React from 'react';
import AppLoad, { Load } from '../AppLoad';
import Styles from './Styles/WelcomePage.module.css'

interface WelcomePageState{
    loading: boolean,
    loaded: boolean,
    progress: number,
    loadingObj: Load | undefined,
}

interface WelcomePageProps{
    onLoad: () => void
}

class WelcomePage extends React.Component<WelcomePageProps, WelcomePageState> {
    constructor(props: WelcomePageProps) {
        super(props);
        this.state = {
            loading: false,
            loaded: false,
            progress: 0,
            loadingObj: undefined
        }
    }

    onLoading = (load: Load | undefined, i: number, count: number) =>{
        this.setState({loadingObj: load, progress: i / count});
        if(load === undefined){
            this.setState({loaded: true});
            setTimeout(() => {
                this.props.onLoad()
            }, 1000);
        }
    }

    componentDidMount(){
        AppLoad.setLoadListener(this.onLoading);
        setTimeout(() => {
            this.setState({loading: true})
            AppLoad.loadAll() 
        }, 500)
    }

    componentWillUnmount(){
        this.setState({loading: false})
    }

    render() {
        return(
            <div className={`${Styles.main} ${this.state.loading ? Styles.loaded : ''}`}>
                <div>
                    <h1>Matrix visualizer</h1>
                    <div className={Styles.pCont}>
                        <div className={Styles.pBar}><div style={{width: `${this.state.progress * 100}%`}}></div></div>
                        <span className={Styles.pTxt}>{this.state.loaded ? "Loaded" : `Loading ${this.state.loadingObj?.element}...`}</span>
                    </div>
                    <p>By: <i>Christian Duarte {"&"} Emiliano Fernandez</i></p>
                </div>
            </div>
        )
    }

}

export default WelcomePage;