import { MathComponent } from "mathjax-react";
import React from "react";
import { MathObjectData, MatrixData, ScalarData } from "../Core/MathObjects/IMathObject";
import MatrixVisualizer from "./MatrixVisualizer";

interface MathObjectVisualizerProps{
    name?: string,
    object: MathObjectData
}

class MathObjectVisualizer extends React.Component<MathObjectVisualizerProps>{
    getLiteral(data: ScalarData, name?: string){
        let returnStr: string = String.raw`<math xmlns = "http://www.w3.org/1998/Math/MathML">`
        if(this.props.name){
            returnStr += String.raw`<mi>${this.props.object.name}</mi>`
            returnStr += String.raw`<mo>=</mo>`
        }
        returnStr += String.raw`<mn>${this.props.object.value}</mn>`
        returnStr += String.raw`</math>`
        return returnStr;
    }

    render(){
        if(this.props.object.type === "scalar"){
            return  <MathComponent mathml={this.getLiteral(this.props.object.value as ScalarData, this.props.name)}/>
        }else{
            return <div>
                <MatrixVisualizer matrixName={this.props.name ?? undefined} matrix={(this.props.object.value as MatrixData).getMatrix()} />
            </div>
        }
    }
}

export default MathObjectVisualizer;