import { MathObjectData } from "./IMathObject";
import MathObjectType from "./MathObjectType";

class SimpleScalarData implements MathObjectData{
    type: MathObjectType = MathObjectType.SCALAR;
    value: number = 0;
    name: string;

    constructor(value: number | string, name: string | undefined = undefined){
        if(typeof value === 'number')
            this.value = value;
        else
            this.name = value;
        if (name)
            this.name = name;
        else
            this.name = this.value.toFixed(2)
    }
}

export default SimpleScalarData;