import { MathComponent } from "mathjax-react";
import React from "react";
import Matrix from "../Core/Matrix";

class MatrixVisualizerProps{
    matrixName?: string
    matrix?: Matrix
}

class MatrixVisualizer extends React.Component<MatrixVisualizerProps>{
    
    getMatrix(){
        let returnStr: string = String.raw`<math xmlns = "http://www.w3.org/1998/Math/MathML">`
        if(this.props.matrixName){
            returnStr += String.raw`<mi>${this.props.matrixName}</mi><mo>=</mo>`
        }
        if(this.props.matrix){
            returnStr += String.raw`<mrow><mo>[</mo>`
            returnStr += String.raw`<mtable>`
            for(let i = 0; i < this.props.matrix.size.rows; i++){
                returnStr += String.raw`<mtr>`;
                for(let j = 0; j < this.props.matrix.size.cols; j++){
                    returnStr += String.raw`<mtd><mn>${this.props.matrix.getValue(i, j)}</mn></mtd>`
                }
                returnStr += String.raw`</mtr>`
            }
            returnStr += String.raw`</mtable>`
            returnStr += String.raw`<mo>]</mo></mrow>`
        }
        returnStr += String.raw`</math>`
        return returnStr
    }

    render(){
        return(
            <MathComponent mathml={this.getMatrix()} />
        )
    }
}

export default MatrixVisualizer;