enum LoadType{
    PAGE = "PAGE"
}

type LoadListener = (load: Load | undefined, index: number, count: number) => void;

interface Load {
    element: string,
    type: LoadType,
    page: string,
}

const LOAD_FUNCS = {
    PAGE: (element: Load) => fetch(element.page + "?e=" + (new Date()).getTime(), {
        mode: 'no-cors',
        credentials: 'omit',
    }),
}

const THINGS_TO_LOAD : Load[]  = [
    {element: "waffles", type: LoadType.PAGE, page: "https://deelay.me/1000/pokeapi.co/api/v2/pokemon/ditto"},
    {element: "vector", type: LoadType.PAGE, page: "https://deelay.me/700/pokeapi.co/api/v2/pokemon/pikachu"},
    {element: "matrices", type: LoadType.PAGE, page: "https://deelay.me/300/pokeapi.co/api/v2/pokemon/bulbasaur"},
    {element: "objects", type: LoadType.PAGE, page: "https://vistapointe.net/images/coconut-10.jpg"},
]

class AppLoad{

    static _loadListener: LoadListener | undefined = undefined;

    static setLoadListener(listener: LoadListener | undefined = undefined){
        this._loadListener = listener;
    }
    
    static async loadAll(){
        let i = 0;
        const count = Object.keys(THINGS_TO_LOAD).length;
        for (const property in THINGS_TO_LOAD) {
            const obj = THINGS_TO_LOAD[property]
            if(this._loadListener)
                this._loadListener(obj, i, count);
            await LOAD_FUNCS[obj.type](obj)
            i++;
        }
        if(this._loadListener)
                this._loadListener(undefined, i, count);
    }
}

export type {AppLoad, Load};
export default AppLoad;