import React, { ChangeEvent, CSSProperties } from 'react';
import Styles from './Styles/TextField.module.css'

type TextFieldType = 
    "number" |
    "text";

interface TextFieldProps{
    placeholder?: string | undefined,
    type?: TextFieldType | undefined,
    min?: number | undefined,
    max?: number | undefined,
    maxLength?: number | undefined
    className?: string | undefined,
    style?: CSSProperties | undefined,
    onChange?: (value: any) => void,
    parser?: (value: string) => any,
    validate?: (value: string) => any,
    initialText?: string | undefined
}

interface TextFieldState{
    text: string,
}

class TextField extends React.Component<TextFieldProps, TextFieldState> {
    value:string;

    constructor(props: TextFieldProps) {
        super(props);
        this.value = props.initialText ?? "";
    }

    change = (e: ChangeEvent<HTMLInputElement>) => {
        if(!this.props.validate || this.props.validate(e.target.value)){
            this.props.onChange && this.props.onChange(this.props.parser ? this.props.parser(e.target.value) : e.target.value);
            this.value = e.target.value;
        }else{
            e.target.value = this.value;
        }
    }

    render() {
        return(
            <input defaultValue={this.props.initialText} maxLength={this.props.maxLength} onBlur={this.change} className={`${Styles.main} ${this.props.className ?? ""}`} size={this.props.placeholder?.length ?? 10} style={this.props.style} type={this.props.type} placeholder={this.props.placeholder} min={this.props.min} max={this.props.max} />
        );
    }
}

export default TextField;