import React from "react";
import VariableInput from "../Components/VariableInput";
import { MathObjectData, MatrixData } from "../Core/MathObjects/IMathObject";
import SimpleMatrixData from "../Core/MathObjects/SimpleMatrixData";
import SimpleScalarData from "../Core/MathObjects/SimpleScalarData";
import Utils from "../utils/Utils";
import Styles from "./Styles/OperationsPanel.module.css"

interface OperationsPanelProps {
    onDataChange: () => void
}

interface InputData {
    [key: string] : MathObjectData;
}

interface OperationsPanelState{
    inputData: InputData
}

class OperationsPanel extends React.Component<OperationsPanelProps, OperationsPanelState>{
    constructor(props: OperationsPanelProps){
        super(props);
        this.state = {
            inputData: {
                "x": new SimpleScalarData(1, "x"),
                "y": new SimpleScalarData(2, "y"),
                "z": new SimpleScalarData(3, "z"),
            }
        }
        window.inputData = {
            "x": new SimpleScalarData(1, "x"),
            "y": new SimpleScalarData(2, "y"),
            "z": new SimpleScalarData(3, "z"),
        }
    }

    scalarValueChange = (k:string, e: number) => {
        let idata = this.state.inputData;
        let value: SimpleScalarData = this.state.inputData[k] as SimpleScalarData;
        value.value = e;
        idata[k] = value;
        this.setState({inputData: idata})
    }

    matrixValueChange = (k:string, e: MatrixData) => {
        let idata = this.state.inputData;
        let value: SimpleMatrixData = this.state.inputData[k] as SimpleMatrixData;
        value.value = e;
        idata[k] = value;
        this.setState({inputData: idata})
    }

    inputDataHasChanged = () => {
        window.inputData = this.state.inputData;
    }

    valueChange = (k:string, elem: any) => {
        console.log(k);
        if(this.state.inputData[k].type === "scalar")
            this.scalarValueChange(k, elem as number)
        if(this.state.inputData[k].type === "matrix")
            this.matrixValueChange(k, elem as MatrixData)
        this.inputDataHasChanged();
    }

    changeKey: ((key:string, newKey:string, element:MathObjectData) => boolean) = (key:string, newKey:string, element:MathObjectData) => {
        if(newKey.match(/^[A-Za-z]+$/) && this.state.inputData[newKey] === undefined && this.state.inputData[key] !== undefined){
            let idata = this.state.inputData;
            let value = idata[key];
            delete idata[key];
            value.name = newKey;
            idata[newKey] = value;
            this.setState({inputData: idata})
            this.inputDataHasChanged();
            return true;
        }
        return false;
    }

    typeChange: ((key:string, element:MathObjectData) => void) = (key: string, element:MathObjectData) => {
        let idata = this.state.inputData;
        delete idata[key];
        idata[key] = element;
        this.setState({inputData: idata})
        this.inputDataHasChanged();
    }

    render(){
        return <div className={Styles.main}>
            <h1>Variables</h1>
            {
                Utils.MapJSON<MathObjectData>(this.state.inputData, (e, k) => (
                    <VariableInput className={Styles.variable} dataKey={k as string} initialInputData={e} key={k as string} typeChange={this.typeChange} onValueChange={(k,e) => this.valueChange(k, e)} askForKeyChange={this.changeKey}/>
                ))
            }
        </div>
    }
}

export default OperationsPanel;
export type {InputData};