import React, { CSSProperties } from "react";
import Styles from './Styles/FlatButton.module.css'

interface FlatButtonProps{
    text?: string,
    disabled?: boolean | undefined,
    className?: string | undefined,
    style?: CSSProperties | undefined,
    click?: ()=>void,
}

class FlatButton extends React.Component<FlatButtonProps>{
    render(){
        return(
            <button className={`${Styles.main} ${this.props.className ?? ""}`} disabled={this.props.disabled} style={this.props.style} onClick={this.props.click}>{this.props.text}</button>
        );
    }
}

export default FlatButton;