import React from "react";
import FlatButton from "../Components/FlatButton";
import TextField from "../Components/TextField";
import Matrix from "../Core/Matrix";
import OperationsPanel from "../Panels/OperationsPanel";
import Styles from './Styles/AppPage.module.css'
import MatrixCalculator from '../Core/MatrixCalculator'
import IMathObject from "../Core/MathObjects/IMathObject";
import MathObjectVisualizer from "../Components/MathObjectVisualizer";

interface AppPageState{
   varsPanel: boolean
   value: string,
   result?: IMathObject | undefined,
   error: string | undefined
}

class AppPage extends React.Component<{}, AppPageState>{

   matrixA: Matrix = new Matrix([
      [3, 2, 1, 0],
      [2, 3, 2, 1],
      [1, 2, 3, 2],
      [0, 1, 2, 3],
   ], [4, 4]);

   //matrixI: Matrix = Matrix.identity(4);
   
   matrixI: Matrix = new Matrix([
      [2, 0, 0, 0],
      [0, 2, 0, 0],
      [0, 0, 2, 0],
      [0, 0, 0, 2],
   ], [4, 4]);
   matrixAI: Matrix = new Matrix([[1]], [1, 1]);

   constructor(props: any){
      super(props);
      this.matrixAI = this.matrixA.mult(this.matrixI);
      this.state = {
         varsPanel: true,
         value: "",
         result: undefined,
         error: undefined
      }
   }

   calculate = ()=>{
      try{
         this.setState({result:MatrixCalculator.parse(this.state.value), error: undefined})
      }catch(e){
         this.setState({result: undefined, error:(e as Error).message})
      }
   }

   render(){
      return (
      <div className={Styles.main}>
         <div className={`${Styles.varsPanel} ${this.state.varsPanel && Styles.varsActive}`}>
            <button className={Styles.varsPanelTab} onClick={() => {this.setState({varsPanel: !this.state.varsPanel})}}>{
               "Variables".split('').map((e, i) => <span key={i}>{e}<br/></span>)
            }</button>
            <div className={Styles.cut}>
               <div className={Styles.container}>
                  <OperationsPanel onDataChange={()=>{}}/>
               </div>
            </div>
         </div>
         <div className={`${Styles.inputPanel} ${this.state.varsPanel && Styles.varsActive}`}>
            <div>
               <TextField placeholder={"Introduzca su expresión matemática"} style={{maxWidth: "90vw", fontSize:"1.6em"}} onChange={(str) => this.setState({value: str})}/>
               <FlatButton text={"Calcular"} click={this.calculate}/>
            </div>
            <div>
               {this.state.result && <>
                  <h1>{'Resultado: '}</h1>
                  <MathObjectVisualizer name={this.state.value} object={this.state.result as IMathObject}/>
               </>}
               {this.state.error && <>
                  <h2 className={Styles.error}>{"Hubo un error, por favor compruebe sus entradas"}</h2>
                  <h3 className={Styles.errorSub}>{'Error: ' + this.state.error}</h3>
               </>}
            </div>
         </div>
      </div>)
   }
}

export default AppPage;