
// Indica el tamaño de la matriz
// Esta en: 
//  - Formato de arreglo, con la posicion 0 siendo el numero de filas y la 1 siendo el numero de columnas
//  - Formato de objeto accediendo al elmento cols para las columas y rows para las filas
interface MatrixDimension{
    0: number;
    1: number;
    cols: number;
    rows: number;
}

// Indica el tamaño de la matriz
// Esta en: 
//  - Formato de arreglo, con la posicion 0 siendo la fila y la 1 siendo la columna
//  - Formato de objeto accediendo al elmento col para la columa y row para la fila
//  - Formato de objeto accediendo a x para la columna y y para la fila
interface MatrixPosition{
    0: number;
    1: number;
    x: number;
    y: number;
    row: number;
    col: number;
}


class Matrix {
    // Valores de la matriz
    // Se accede a ellos de igual forma que en algebra, primero la fila y luego columna
    values:number[][];
    // Tamaño de la matriz
    size: MatrixDimension;

    // Constructor toma como parametros la matriz como un arreglo bidimencional y el tamaño de la matriz
    constructor(values: number[][], size?: number[] | undefined, __dangerouslySetWithoutVerification:boolean = false){
        this.size = {0: 0, 1: 0, cols: 0, rows: 0};
        if(!size)
            this.setSize(values.length, values[0].length);
        else
            this.setSize(size[1], size[0]);
        this.values = [];
        if(!__dangerouslySetWithoutVerification){
            this.init(values);
        }else{
            this.values = values;
        }
    }

    // Asigna el tamaño de la matriz
    setSize(x: number = 0, y: number = 0){
        this.size = {0: y, 1: x, cols: x, rows: y};
    }

    // Obtiene un valor de la matriz, toma como parametros la fila y la columna
    getValue(y: number | undefined = undefined, x: number | undefined = undefined){
        if(y === undefined){
            return null;
        }
        if(x === undefined){
            return this.values[y];
        }
        return this.values[y][x]
    }

    // Crea una matriz identidad del tamaño indicado
    static identity(size: number = 4){
        let values = new Array(size);
        for (let y = 0; y < size; y++) {
            values[y] = new Array(size).fill(0);
            values[y][y] = 1;
        }
        return new Matrix(values, [size, size], true);
    }

    // Inicializa los valores del arreglo
    init(values:number[][] | number[] | number | undefined = undefined){
        if(typeof values === 'number'){
            this.values = new Array(this.size.rows);
            for (let y = 0; y < this.size.rows; y++) {
                this.values[y] = new Array(this.size.cols).fill(values);
            }
        } if(Array.isArray(values)){
            this.values = new Array(this.size.rows);
            for (let y = 0; y < this.size.rows; y++) {
                this.values[y] = new Array(this.size.cols).fill(0);
            }
            let vector: boolean = !Array.isArray(values[0]);
            let lengthsRight = true;
            
            if(vector)
                lengthsRight = values.length >= this.size[0] * this.size[1];
            else{
                if(values.length < this.size[0])
                    lengthsRight = false
                else
                    this.values.some((v) => {
                        if(v.length < this.size[1]){
                            lengthsRight = false;
                            return true;
                        }
                        return false;
                    })
            }

            if(!lengthsRight){
                throw new Error("Wrong lengths in matrix");
            }

            for (let y = 0; y < this.size.rows; y++){
                for(let x = 0; x < this.size.cols; x++){
                    if(typeof values[y] == "number"){
                        this.values[y][x] = values[x + this.size.cols * y] as number;
                    }else{
                        this.values[y][x] = (values as number[][])[y][x];
                    }
                }
            }
        }
    }

    // Suma esta instancia de matriz con otra matriz o con un escalar
    plus(matrix: Matrix | number){
        let returnMatrixArray = [];
        for (let y = 0; y < this.size.rows; y++) {
            returnMatrixArray.push(new Array(this.size.cols).fill(0))
        }
        if(matrix instanceof Matrix){
            if(this.size.cols !== matrix.size.cols || this.size.rows !== matrix.size.rows){
                throw new Error("Wrong addition matrix size");
            }
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] + matrix.values[y][x];
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }else if(typeof matrix === 'number'){
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] + matrix;
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }
        throw new Error("Wrong addition parameters");
    }
    
    // Resta esta instancia de matriz con otra matriz o con un escalar
    minus(matrix : Matrix | number){
        let returnMatrixArray = [];
        for (let y = 0; y < this.size.rows; y++) {
            returnMatrixArray.push(new Array(this.size.cols).fill(0))
        }
        if(matrix instanceof Matrix){
            if(this.size.cols !== matrix.size.cols || this.size.rows !== matrix.size.rows){
                throw new Error("Wrong subtraction matrix size");
            }
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] - matrix.values[y][x];
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }else if(typeof matrix === 'number'){
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] - matrix;
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }
        throw new Error("Wrong subtraction parameters");
    }

    // Multiplica esta instancia de matriz con otra matriz o con un escalar
    mult(matrix : number | Matrix){
        if(matrix instanceof Matrix){
            if(this.size.cols !== matrix.size.rows){
                throw new Error("Wrong multiplication matrix size");
            }
            let returnMatrixArray = [];
            for (let y = 0; y < this.size.rows; y++) {
                returnMatrixArray.push(new Array(matrix.size.cols).fill(0))
            }
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < matrix.size.cols; x++){
                    let sum = 0;
                    for(let i = 0; i < this.size.cols; i++){
                        sum += this.values[y][i] * matrix.values[i][x];
                    }
                    returnMatrixArray[y][x] = sum;
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, matrix.size.cols], true);
        }else if(typeof matrix === 'number'){
            return this.scale(matrix);
        }
        throw new Error("Wrong multiplication parameters");
    }

    // Divide esta instancia de matriz con un escalar
    divide(scale : number){
        if(typeof scale === 'number'){
            let returnMatrixArray = [];
            for (let y = 0; y < this.size.rows; y++) {
                returnMatrixArray.push(new Array(this.size.cols).fill(0))
            }
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] / scale;
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }
        throw new Error("Wrong scale parameter");
    }

    // Multiplica esta instancia de matriz con un escalar
    scale(scale : number){
        if(typeof scale === 'number'){
            let returnMatrixArray = [];
            for (let y = 0; y < this.size.rows; y++) {
                returnMatrixArray.push(new Array(this.size.cols).fill(0))
            }
            for (let y = 0; y < this.size.rows; y++) {
                for(let x = 0; x < this.size.cols; x++){
                    returnMatrixArray[y][x] = this.values[y][x] * scale;
                }
            }
            return new Matrix(returnMatrixArray, [this.size.rows, this.size.cols], true);
        }
        throw new Error("Wrong scale parameter");
    }

    // Obtiene la matriz opuesta de esta instancia matriz
    opposite(){
        return this.scale(-1);
    }

    // Obtiene la matriz traspuesta de esta instancia matriz
    tras(){
        let returnMatrixArray = [];
        for (let x = 0; x < this.size.cols; x++) {
            returnMatrixArray.push(new Array(this.size.rows).fill(0))
        }
        for (let y = 0; y < this.size.cols; y++) {
            for(let x = 0; x < this.size.rows; x++){
                returnMatrixArray[y][x] = this.values[x][y];
            }
        }
        return new Matrix(returnMatrixArray, [this.size.cols, this.size.rows], true);
    }

    // Clona esta instancia matriz
    clone(){
        return new Matrix(this.values, [this.size.rows, this.size.cols]);
    }

    // Itera cada elemento de esta amtriz
    forEach(callbackF : (value: number, index: MatrixPosition, array: number[][]) => void, startingAt1 : boolean){
        if(typeof callbackF !== 'function'){
            throw new TypeError(callbackF + " is not a function");
        }
        let iteration : MatrixPosition = {0: 0, 1: 0, x: 0, y: 0, row: 0, col: 0};
        for (let y = 0; y < this.size.rows; y++) {
            for(let x = 0; x < this.size.cols; x++){
                let m = startingAt1 ? 1 : 0;
                iteration[0] = y + m;
                iteration[1] = x + m;
                iteration.x = x + m;
                iteration.y = y + m;
                iteration.row = y + m;
                iteration.col = x + m;
                callbackF(this.values[y][x], iteration, this.values);
            }
        }
    }

    // Obtiene el determinante de esta instancia matriz
    determinant: (()=> number) = () => {
        if(this.size.cols === this.size.rows){
            if(this.size.cols > 2){
                let returnValue: number = 0;
                for (let startI = 0; startI < this.size.cols; startI++) {
                    let mult = 1;
                    for (let i = 0; i < this.size.cols; i++) {
                        mult *= this.values[i][Math.abs((i + startI) % this.size.cols)];
                    }
                    returnValue += mult;
                }
                for (let startI = 0; startI < this.size.cols; startI++) {
                    let mult = 1;
                    for (let i = 0; i < this.size.cols; i++) {
                        let n = (startI - i) < 0 ? this.size.cols - Math.abs((startI - i) % this.size.cols) : Math.abs((startI - i) % this.size.cols)
                        mult *= this.values[i][n];
                    }
                    returnValue -= mult;
                }
                return returnValue;
            }else if(this.size.cols === 2){
                return this.values[0][0] * this.values[1][1] - this.values[0][1] * this.values[1][0];
            }else{
                return this.values[0][0]
            }
        }
        throw new Error("Solo a las matrices cuadradas se les puede sacar un determinante")
    }

    // Obtiene el cofactor ij de esta instancia matriz, toma como parametros la fila i la columna del cofactor a obtener
    cofactor(row: number, col: number, startAtOne?: boolean | undefined){
        if(this.size.cols !== this.size.rows)
            throw new Error("No se puede sacar el cofactor de una matriz que no es cuadrada")
        let ir = 0, ic = 0;
        let minor: number[][] = new Array(this.size.rows - 1);
        for (let l = 0; l < minor.length; l++) {
            minor[l] = new Array(this.size.cols - 1);
        }
        for (let i = 0; i < this.values.length; i++) {
            for (let j = 0; j < this.values.length; j++) {
                if( (startAtOne ? i + 1 : i) === row || (startAtOne ? j + 1 : j) === col){
                    continue
                }
                minor[ir][ic] = this.values[i][j];
                ic++;
                if(ic >= minor.length){
                    ic = 0;
                    ir++;
                }
            }
        }
        let m: Matrix = new Matrix(minor, [minor.length, minor.length]);
        return m.determinant() * ((row+col) % 2 < .5 ? 1 : -1);
    }

    // Obtiene la matriz de cofactores
    cofactorMatrix(){
        if(this.size.cols !== this.size.rows)
            throw new Error("No se puede sacar la matriz de cofactores de una matriz que no es cuadrada")
        let coMatrix: number[][] = new Array(this.size.rows);
        for (let l = 0; l < coMatrix.length; l++) {
            coMatrix[l] = new Array(this.size.cols);
        }
        this.forEach((v, pos) => {
            coMatrix[pos.row][pos.col] = this.cofactor(pos.row, pos.col);
        }, false);
        return new Matrix(coMatrix, [this.size.rows, this.size.cols]);
    }

    // Obtiene la adjunta según la mayoria de información que he recolectado de internet
    adjugate(){
        return this.cofactorMatrix().tras();
    }

    // Retorna la matriz inversa de esta instancia matriz
    inv(){
        return this.adjugate().divide(this.determinant())
    }
}

export default Matrix;