import React, { CSSProperties } from "react";
import Styles from './Styles/Combobox.module.css';
interface ComboboxProps<T>{
    items: T[],
    index?: number | undefined,
    className?: string | undefined,
    style?: CSSProperties | undefined
    onChange?: (str: T) => void 
}

class Combobox<T> extends React.Component<ComboboxProps<T>>{

    render(){
        return <select value={this.props.index} className={`${Styles.main} ${this.props.className ?? ""}`} style={this.props.style} onChange={(e) => this.props.onChange && this.props.onChange(this.props.items[e.target.selectedIndex])}>
            {this.props.items.map((e, index) => {
                return <option key={index} value={index}>
                    {e}
                </option>
            })}
        </select>;
    }
}

export default Combobox;