import React from "react";
import { MathObjectData, MatrixData } from "../Core/MathObjects/IMathObject";
import MathObjectType from "../Core/MathObjects/MathObjectType";
import TextField from "./TextField";
import Styles from './Styles/VariableInput.module.css';
import Combobox from "./Combobox";
import SimpleMatrixData from "../Core/MathObjects/SimpleMatrixData";
import SimpleScalarData from "../Core/MathObjects/SimpleScalarData";

interface VariableInputProps{
    dataKey: string,
    initialInputData?: MathObjectData | undefined,
    askForKeyChange: (key: string, newKey: string, data: MathObjectData) => boolean;
    onValueChange: (key: string, element: any) => void;
    typeChange: (key: string, element: MathObjectData) => void;
    className?: string | undefined
}

interface VariableInputState {
    value: MathObjectData
}

class VariableInput extends React.Component<VariableInputProps, VariableInputState> {
    
    constructor(props: VariableInputProps){
        super(props);
        this.state = {
            value: props.initialInputData ?? {
                name: props.dataKey ?? "x",
                type: MathObjectType.SCALAR,
                value: 0
            }
        }
    }

    keyChange = (newKey: string) => {
        this.props.askForKeyChange(this.props.dataKey, newKey, this.state.value)
    }

    typeChange = (newType: MathObjectType) => {
        if(this.state.value.type !== newType){
            let value: SimpleMatrixData | SimpleScalarData;
            if(newType === MathObjectType.MATRIX){
                value = new SimpleMatrixData(this.state.value.name, [[this.state.value.value as number]], 1, 1);
            }
            else{
                value =  new SimpleScalarData((this.state.value.value as MatrixData).values[0][0], this.state.value.name);
            }
            this.setState({
                value: value
            });
            this.props.typeChange(this.state.value.name, value)
        }
    }

    parserMatrixSize = (val:string) => {
        return !isNaN(parseInt(val)) && parseInt(val) > 0 && parseInt(val) < 6;
    }

    matrixSizeChange = (rows?: number|undefined, cols?: number | undefined) =>{
        let a:MatrixData = this.state.value.value as MatrixData;
        let lastValues: number[][] = a.values; 
        if(rows === undefined)
            rows = a.rows;
        if(cols === undefined)
            cols = a.cols;
        a.values = new Array(rows);
        for (let row = 0; row < rows; row++) {
            a.values[row] = new Array(cols);
            for (let col = 0; col < cols; col++) {
                if(row < lastValues.length && col < lastValues[row].length)
                    a.values[row][col] = lastValues[row][col];
                else
                    a.values[row][col] = 0;
            }
        }
        a.rows = rows;
        a.cols = cols;
        this.props.onValueChange(this.props.dataKey, a);
        this.setState({value:  new SimpleMatrixData(this.state.value.name, a.values, a.rows, a.cols)})
    }

    scalarValueChange = (e: number) => {
        this.props.onValueChange(this.props.dataKey, e);
    }

    matrixValueChange = (row: number, col: number, value: number) => {
        let a:MatrixData = this.state.value.value as MatrixData;
        if(a.cols > col && a.rows > row){
            a.values[row][col] = value;
            this.props.onValueChange(this.props.dataKey, a);
            this.setState({value:  new SimpleMatrixData(this.state.value.name, a.values, a.rows, a.cols)})
        }
    }

    render(){
        return <div className={`${Styles.main} ${this.props.className ?? ""}`}>
            <div className={Styles.varDefArea}>
                <span>Nombre:</span>
                <TextField className={Styles.customTextField} style={{width:"5ch", textAlign:"center"}} maxLength={1} initialText={this.state.value.name} onChange={this.keyChange}/>
                <span>Tipo:</span>
                <Combobox<MathObjectType> items={[MathObjectType.MATRIX, MathObjectType.SCALAR]} className={Styles.customComboBox} style={{flex:1}} onChange={this.typeChange} index={[MathObjectType.MATRIX, MathObjectType.SCALAR].indexOf(this.state.value.type)}/>
            </div>
            {this.state.value.type === MathObjectType.MATRIX && <>
                <div className={Styles.varDefArea}>
                    <span>Filas:</span>
                    <TextField className={Styles.customTextField} style={{flex:1}} initialText={(this.state.value.value as MatrixData).rows.toFixed()} maxLength={2} parser={parseFloat} validate={this.parserMatrixSize} onChange={e => this.matrixSizeChange(e, undefined)}/>
                    <span>Columnas:</span>
                    <TextField className={Styles.customTextField} style={{flex:1}} initialText={(this.state.value.value as MatrixData).cols.toFixed()} maxLength={2} parser={parseFloat} validate={this.parserMatrixSize} onChange={e => this.matrixSizeChange(undefined, e)}/>
                </div>
                <div className={Styles.table}>
                    {(this.state.value.value as MatrixData).values.map((e, row) => 
                        <div key={row} className={Styles.row}>
                            {e.map((elem, col) => <TextField initialText={elem.toFixed()} className={Styles.customTextField} key={`${row} ${col}`} style={{flex:1, width: 0}} parser={parseFloat} validate={(n) => !isNaN(parseFloat(n))} onChange={val => this.matrixValueChange(row, col, val)}/>)}
                        </div>
                    )}
                    
                </div>
            </>}
            {this.state.value.type === MathObjectType.SCALAR && <>
                <div className={Styles.varDefArea}>
                    <span>Valor:</span>
                    <TextField initialText={(this.state.value.value as number).toFixed()} onChange={this.scalarValueChange} className={Styles.customTextField} style={{flex:1}} parser={parseFloat} validate={(n) => !isNaN(parseFloat(n))}/>
                </div>
            </>}
        </div>
    }
}

export default VariableInput;