import './App.css';
import React from 'react';
import WelcomePage from './Pages/WelcomePage';
import AppPage from './Pages/AppPage';

interface AppState{
  loaded: boolean
}

class App extends React.Component<{}, AppState> {
  constructor(props: {}){
    super(props);
    this.state = {
      loaded: false
    }
  }

  onLoad = () => {
    this.setState({loaded: true})
  }

  render(){
    return (
      <div className="App">
        {/* <TextField placeholder="X" value={0}></TextField> */}
        {!this.state.loaded && <WelcomePage onLoad={this.onLoad}/>}
        {this.state.loaded && <AppPage />}
      </div>
    );
  }
}

export default App; 
